@charset 'utf-8';
#animate {
  height: 100vh;
  width: 100vw;
}
#animate span {
  font-size: 40px;
}
#animate .box {
  width: 50px;
  height: 50px;
  background: #ccc;
  -webkit-animation: mybox 1s ease;
  animation: mybox 1s ease;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
  /* 动画停留在最终状态 */
}
@keyframes mybox {
  0% {
    width: 50px;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
    background: #ddd;
  }
  50% {
    background: #ddd;
    width: 50px;
    -webkit-transform: rotate(270deg);
    transform: rotate(270deg);
  }
  70% {
    background: #ddd;
    width: 50px;
  }
  85% {
    width: 100px;
    background: #ffff0030;
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
  100% {
    -webkit-transform: translate(-90%);
    transform: translate(-90%);
  }
}
@-webkit-keyframes mybox {
  0% {
    width: 50px;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
    background: #ddd;
  }
  50% {
    background: #ddd;
    width: 50px;
    -webkit-transform: rotate(270deg);
    transform: rotate(270deg);
  }
  70% {
    background: #ddd;
    width: 50px;
  }
  85% {
    width: 100px;
    background: #ffff0030;
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
  100% {
    -webkit-transform: translate(-90%);
    transform: translate(-90%);
  }
}
